<template>
  <div class="hello">

    <img v-if="this.search === 'confirmed'"
      src="https://jornaleconomico.sapo.pt/wp-content/uploads/2020/03/coronavirus-4833754_960_720.jpg" />
    <img v-if="this.search === 'deaths'" src="https://images.theconversation.com/files/78920/original/image-20150422-1833-sqokof.jpg?ixlib=rb-1.1.0&q=45&auto=format&w=926&fit=clip" />
    <img v-if="this.search === 'recovered'"
      src="https://i.insider.com/5e7ba2f5487c2211ce375394?width=1100&format=jpeg&auto=webp" />

    <h1>{{this.search}}</h1>

    <h3>Date {{currentTime}} </h3>
    <table>
      <tr>
        <th>Lugar</th>
        <th>Country</th>
        <th>{{this.search}}</th>
      </tr>

      <tr v-for="countryInfo in countryInfos" :key="countryInfo.country">
        <td>#{{countryInfo.rank}}</td>
        <td>{{countryInfo.country}}</td>
        <td>{{countryInfo.info}}</td>
      </tr>

    </table>
  </div>
</template>

<script>
  export default {
    name: "HelloWorld",
    props: {
      countries: Number,
      search: String
    },
    data() {
      return {
        covid: {},
        countryInfos: [],
        currentTime: ""
      }
    },
    async created() {
      const response = await fetch('https://api.covid19api.com/summary')
      const myData = await response.json()
      let countryInfos = []
      if (this.search == "confirmed") {
        for (let i = 0; i < myData.Countries.length; i++) {
          countryInfos.push({
            country: myData.Countries[i].Country,
            info: myData.Countries[i].TotalConfirmed
          })
        }
      } else if (this.search == "deaths") {
        for (let i = 0; i < myData.Countries.length; i++) {
          countryInfos.push({
            country: myData.Countries[i].Country,
            info: myData.Countries[i].TotalDeaths
          })
        }
      } else if (this.search == "recovered") {
        for (let i = 0; i < myData.Countries.length; i++) {
          countryInfos.push({
            country: myData.Countries[i].Country,
            info: myData.Countries[i].TotalRecovered
          })
        }
      }
      countryInfos.sort(function (a, b) {
        return b.info - a.info
      })
      let finalResult = []
      for (let i = 0; i < this.countries; i++) {
        finalResult.push({
          country: countryInfos[i].country,
          info: countryInfos[i].info
        })
      }
      var today = new Date()
      var dd = today.getDate();
      var mm = today.getMonth() + 1; 
      var yyyy = today.getFullYear();
      if (dd < 10) dd = '0' + dd;
      if (mm < 10) mm = '0' + mm;
      this.currentTime = `${dd}/${mm}/${yyyy}`
      this.countryInfos = finalResult     

    },
    async updated() {
      const response = await fetch('https://api.covid19api.com/summary')
      const myData = await response.json()
      let countryInfos = []
      if (this.search == "confirmed") {
        for (let i = 0; i < myData.Countries.length; i++) {
          countryInfos.push({
            country: myData.Countries[i].Country,
            info: myData.Countries[i].TotalConfirmed
          })
        }
      } else if (this.search == "deaths") {
        for (let i = 0; i < myData.Countries.length; i++) {
          countryInfos.push({
            country: myData.Countries[i].Country,
            info: myData.Countries[i].TotalDeaths
          })
        }
      } else if (this.search == "recovered") {
        for (let i = 0; i < myData.Countries.length; i++) {
          countryInfos.push({
            country: myData.Countries[i].Country,
            info: myData.Countries[i].TotalRecovered
          })
        }
      }
      countryInfos.sort(function (a, b) {
        return b.info - a.info
      })
      let finalResult = []
      for (let i = 0; i < this.countries; i++) {
        finalResult.push({
          rank: i + 1,
          country: countryInfos[i].country,
          info: countryInfos[i].info
        })
      }
      var today = new Date()
      var dd = today.getDate();
      var mm = today.getMonth() + 1;
      var yyyy = today.getFullYear();
      if (dd < 10) dd = '0' + dd;
      if (mm < 10) mm = '0' + mm;
      this.currentTime = `${dd}/${mm}/${yyyy}`
      this.countryInfos = finalResult      
    
    }
  };
</script>

